import React from 'react';
import Img from 'gatsby-image';
import { Link } from 'gatsby';
import classes from './EachShopItem.module.scss';
import ProductPrice from '../ProductPrice/ProductPrice';

function EachShopItem({ title, price, img }) {
	return (
		<Link key={title} to={`/product/${title}`}>
			<div className={classes.item}>
				<div className="d-flex justify-content-center">
					<div className="mt-5">
						{typeof img === 'string' ? (
							<img className={`img-fluid ${classes.itemImg}`} alt="img" src={img} />
						) : (
							<Img fluid={img} className={classes.itemImg} />
						)}
					</div>
				</div>
				<p className={`text-right mr-3 mt-3 ${classes.itemp}`}>{title}</p>
				<ProductPrice cls="pb-3" price={price} />
			</div>
		</Link>
	);
}

export default EachShopItem;
