import React from 'react';

function ProductPrice({ price, cls }) {
	return (
		<h5 dir="rtl" className={`text-left m-0 p-0 ml-4 ${cls}`}>
			{price.toLocaleString('ar-EG')}
			<span className="mr-2" style={{ fontWeight: 'lighter' }}>
				تومان
			</span>
		</h5>
	);
}

export default ProductPrice;
