import React, { useEffect, useState } from 'react';
import { graphql, navigate } from 'gatsby';
import { Pagination, Spin } from 'antd';
import queryString from 'query-string';
import { LoadingOutlined } from '@ant-design/icons';

// Components
import Layout from '../Layout/Layout';
import Filter from '../Filter/Filter';
import EachShopItem from '../EachShopItem/EachShopItem';
import ShopItemContainer from '../ShopItemContainer/ShopItemContainer';

const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;
const contentful = require('contentful');
export const query = graphql`
	query($limit: Int!, $skip: Int!) {
		allContentfulProduct(skip: $skip, limit: $limit) {
			edges {
				node {
					title {
						content {
							content {
								value
							}
						}
					}
					desc {
						content {
							content {
								value
							}
						}
					}
					link {
						content {
							content {
								value
							}
						}
					}
					category
					price
					img {
						fluid {
							...GatsbyContentfulFluid
						}
					}
				}
			}
		}
	}
`;
const client = contentful.createClient({
	space: `${process.env.SPACE_ID}`,
	accessToken: `${process.env.ACCESS_TOKEN}`
});

function Shop(props) {
	const [ fetched, setFetched ] = useState([]);
	const [ total, setTotal ] = useState(1);
	const [ fetching, setFetch ] = useState(false);
	const pageNum = props.pageContext.numberOfPages * props.pageContext.limit;
	const parsed = queryString.parse(props.location.search);

	const handlePage = (num) => {
		if (num === 1) {
			navigate(`/shop`);
		} else {
			navigate(`/shop/${num}`);
		}
	};

	const handleSort = (val) => {
		if (val === 'expensive') {
			navigate(`/shop?sort=desc&page=1`);
		} else {
			navigate(`/shop?sort=asc&page=1`);
		}
	};

	const handleFetch = (val, page) => {
		let skip;
		let limit = 6;
		if (page === '1') {
			skip = 0;
		} else {
			skip = JSON.parse(page - 1) * limit;
		}

		setFetch(true);

		client
			.getEntries({
				content_type: 'product',
				order: val,
				skip: skip,
				limit: limit
			})
			.then((res) => {
				setFetched(res.items);
				setTotal(res.total);
				setFetch(false);
			})
			.catch((err) => {
				console.log(err.message);
				setFetch(false);
			});
	};

	useEffect(
		() => {
			let order;
			if (parsed.sort === 'desc') {
				order = '-fields.price';
				handleFetch(order, parsed.page);
			}
			if (parsed.sort === 'asc') {
				order = 'fields.price';
				handleFetch(order, parsed.page);
			}
		},
		[ props.location.search ]
	);
	return (
		<Layout title="خرید آنلاین">
			<Filter url={'/shop'} location={props.location} handleSort={handleSort} />
			<Spin size="large" spinning={fetching} indicator={antIcon}>
				<ShopItemContainer>
					{props.location.search ? (
						fetched.map((each) => {
							const title = each.fields.title.content[0].content[0].value;
							return (
								<EachShopItem
									key={title}
									title={title}
									img={each.fields.img[0].fields.file.url}
									price={each.fields.price}
								/>
							);
						})
					) : (
						props.data.allContentfulProduct.edges.map((each) => {
							const title = each.node.title.content[0].content[0].value;
							return (
								<EachShopItem
									key={title}
									title={title}
									img={each.node.img[0].fluid}
									price={each.node.price}
								/>
							);
						})
					)}
				</ShopItemContainer>
			</Spin>
			<div className="container d-flex justify-content-center">
				{props.location.search ? (
					<Pagination
						current={JSON.parse(parsed.page)}
						pageSize={6}
						total={total}
						onChange={(num) => navigate(`/shop?sort=${parsed.sort}&page=${num}`)}
						className="mt-3"
					/>
				) : (
					<Pagination
						current={props.pageContext.humanPageNumber}
						pageSize={6}
						total={pageNum}
						onChange={handlePage}
						className="mt-3"
					/>
				)}
			</div>
		</Layout>
	);
}

export default Shop;
